export default {
	data() {
		return {
			minT:20.1,
			maxT:30.1,
			minH:10.1,
			maxH:40.1,
			red_light: false,
			blue_light: false,
			green_light: false,
			img_path: '0'
		}
	},
	methods: {
		change(num) {
			switch (num) {
				case 1:
					this.red_light = !this.red_light;
					break;
				case 2:
					this.green_light = !this.green_light;
					break;
				case 3:
					this.blue_light = !this.blue_light;
					break;
			}
			let red = this.red_light ? 100 : 0;
			let green = this.green_light ? 10 : 0;
			let blue = this.blue_light ? 1 : 0;
			let number = red + green + blue;
			this.img_path = number.toString();
		}
	}
}
